	import app from '@/common/app.js'; //公共变量
	import request from '@/common/request.js';
	import '@/static/css/index.css';
	import util from '@/common/util.js';
	import segmentedControl from '@/components/uni-segmented-control/uni-segmented-control.vue';
	import uniLoadMore from "@/components/uni-load-more/uni-load-more.vue";
	import uParse from '@/components/uParse/src/wxParse.vue'
	import {
		mapState
	} from 'vuex';
	export default {
		components: {
			segmentedControl,
			uniLoadMore,
			uParse
		},
		data() {
			return {
				items: ['响应伙伴'],
				percent: 0,
				id: '',
				title: '',
				pic: '',
				startdate: '',
				enddate: '',
				targetMoney: 222222,
				gotMoney: 111,
				donatingNum: 0,
				name: '',
				content: '',
				donProject: {},
				donPersons: [],
				status: "more",
				imagePath: app.imagePath,
				contentText: {
					contentdown: '加载更多',
					contentrefresh: '加载中',
					contentnomore: '没有更多'
				},
				moreflag: false,
				pageSize: 10,
				pages: 0,
                is_no: app.is_str.is_no

			}
		},
		onReachBottom() {

		},
		onLoad(options) {
			// this.id = decodeURIComponent(options.id);
			this.id = JSON.parse(decodeURIComponent(options.id));
			this.getTeamDonDetail();
		},
		onShow() {

		},
		onShareAppMessage(res) {
			 var name = '';
			if (this.userInfo.name != undefined || this.userInfo.name != null) {
			    name = this.userInfo.name;
			}
			if (this.userInfo.sysUser != undefined && this.userInfo.sysUser.virtualName != null) {
			    name = this.userInfo.sysUser.virtualName;
			}
			if (res.from === 'button') { // 来自页面内分享按钮
			}
			var truePath =encodeURIComponent(JSON.stringify( '/pages/don/teamDonDetail?id='+encodeURIComponent(JSON.stringify(this.id))));
			var img = this.imagePath + this.pic
			if(this.pic==null ||this.pic == ''){
			    img = '/static/img/man_default.png';
			}
            return {
			    title: name + '发起了一起捐，邀请你一起为母校助力',
			    path: '/pages/tabbar/index/index?truePath='+truePath,
			    imageUrl: img
			}
		},
		computed: {
			...mapState(['hasLogin']),
			...mapState(['userInfo'])
		},
		onReachBottom() {
			this.status = "loading";
				if (this.moreflag) {
					this.getDonPersons();
				} else {
					this.status = "noMore";
				}
		},
		methods: {
			getTeamDonDetail() {
				console.log("获取一起捐项目详情")
				request.http({
					url: "/app/don/getteamDonDetail",
					data: {
						id: this.id,
					},
					header: 'application/x-www-form-urlencoded',
					success: (res) => {
						if (res.data.code == app.RESPONSE_STATUS.REQUEST_SUCCESS) {
							this.donProject = res.data.data.donProject;
							this.gotMoney = res.data.data.gotMoney;
							this.targetMoney = res.data.data.targetMoney;
							this.percent = util.toFix((this.gotMoney / this.targetMoney)* 100);
							this.title = res.data.data.name;
							this.pic = res.data.data.smSchoolmate.truePhoto;
							this.startdate = res.data.data.startdate;
							this.enddate = res.data.data.enddate;
							this.donatingNum = res.data.data.personNum;
							this.name = res.data.data.smSchoolmate.name;
							this.content = null != res.data.data.summary?res.data.data.summary:"";
                            this.content = util.strcharacterDiscode(this.content);
							this.getDonPersons();
						}
					},
					fail: (data, code) => {
						console.log(data);
					}
				})
			},
			getDonPersons() {
				this.pages = this.pages + 1;
				request.http({
					url: "/app/don/getDonRecordByPro",
					data: {
						pageNo: this.pages,
						pageSize: this.pageSize,
						donProjectId: this.donProject.id,
						togetherId: this.id
					},
					header: 'application/x-www-form-urlencoded',
					success: (res) => {
						if (res.data.code == app.RESPONSE_STATUS.REQUEST_SUCCESS) {
							var datas = res.data.list;
							datas.forEach((d) => {
								this.donPersons.push(d);
							});
							this.moreflag = (res.data.pages > (this.pages) ? true : false);
							if (this.moreflag) {
									this.status = "noMore";
							}
						}
					},
					fail: (data, code) => {
						console.log(data);
					}
				})
			},
			// goDonPro(detail) {
			// 	var url = '/pages/don/donDetail?id=' + encodeURIComponent(this.donProject.id);
			// 	uni.navigateTo({
			// 		url
			// 	});
			// },
			goImmediateDon(detail) {
				var url = '/pages/don/ImmediateDon?id=' + encodeURIComponent(JSON.stringify(this.id));
				uni.navigateTo({
					url
				});
			},
			// share() {
			// 	uni.share({
			// 		provider: 'weixin',
			// 		type: 5,
			// 		imageUrl: 'https://img-cdn-qiniu.dcloud.net.cn/uniapp/app/share-logo@3.png',
			// 		title: '欢迎体验uniapp',
			// 		miniProgram: {
			// 			id: 'gh_abcdefg',
			// 			path: 'pages/index/index',
			// 			type: 0,
			// 			webUrl: 'http://uniapp.dcloud.io'
			// 		},
			// 		success: ret => {
			// 			console.log(JSON.stringify(ret));
			// 		}
			// 	});
			// },
			goDetail(detail) {
				var url = '/pages_mine/myAssociation/memberDetail?id=' + encodeURIComponent(JSON.stringify(detail.userId));
                if (!this.hasLogin) {
                    uni.showToast({
                        icon: 'none',
                        title: '您没有登录，请先登录！'
                    });
                } else if (this.userInfo.cardStatus != 'NORMAL') {
                    uni.showToast({
                        icon: 'none',
                        title: '您个人信息没有审核通过，请先去认证！'
                    });
                } else if (detail.isShow != app.is_str.is_no) {
                    uni.navigateTo({
                        url
                    });
                }
			},
		}
	}
